
:root {
  --mainHeight: calc(1rem - 0.01rem);
  --color: white;
}
.upload-file-wrap {
  width: calc(1rem - 0.01rem);
  width: var(--mainHeight);
  display: inline-block;
  background: white;
  background: var(--color);
}
.upload-file {
  width: 2rem;
  opacity: 0;
}
.upload-file-wrap:hover {
  opacity: 0.8;
}